import Vue from 'vue'
// 
import {
    Swipe,
    SwipeItem
} from 'vant';
import {
    Lazyload
} from 'vant';
// 
import {
    Grid,
    GridItem
} from 'vant';
//
import {
    Tabbar,
    TabbarItem
} from 'vant';
//
import {
    Icon
} from 'vant';
//
import {
    CountDown
} from 'vant';
// 
import {
    Tab,
    Tabs
} from 'vant';
// 
import {
    Sidebar,
    SidebarItem
} from 'vant';
//
import {
    Search
} from 'vant';
//
import {
    DropdownMenu,
    DropdownItem
} from 'vant';
//
import {
    GoodsAction,
    GoodsActionIcon,
    GoodsActionButton
} from 'vant';
//
import { Card } from 'vant';
//
import { SubmitBar } from 'vant';
//
import { Checkbox, CheckboxGroup } from 'vant'
//
import { Button } from 'vant';
//
import { Field } from 'vant';
//
import { Cell, CellGroup } from 'vant'



export default () => {
    Vue.use(Swipe).use(SwipeItem);
    Vue.use(Lazyload);
    Vue.use(Grid).use(GridItem);
    Vue.use(Tabbar).use(TabbarItem);
    Vue.use(Icon);
    Vue.use(CountDown);
    Vue.use(Tab).use(Tabs);
    Vue.use(Sidebar);
    Vue.use(SidebarItem);
    Vue.use(Search);
    Vue.use(DropdownMenu).use(DropdownItem);
    Vue
        .use(GoodsAction)
        .use(GoodsActionIcon)
        .use(GoodsActionButton);
        Vue.use(Card);
        Vue.use(SubmitBar);
        Vue.use(Checkbox).use(CheckboxGroup);
        Vue.use(Button);
        Vue.use(Field);
        Vue.use(Cell).use(CellGroup);






}